"use client"
import { Canvas } from "@react-three/fiber"
import { OrbitControls, Grid, PerspectiveCamera, Text } from "@react-three/drei"
import { Suspense } from "react"
import * as THREE from "three"

interface Container {
  id: string
  name: string
  type: string
  dimensions: string // e.g., "20x8x8" (length x width x height in feet)
  weight: string
  icon: string
}

interface ViewportCanvasProps {
  container: Container | null
  items: any[]
}

// Parse dimensions string like "20x8x8" into {length, width, height}
function parseDimensions(dimensions: string) {
  const [length, width, height] = dimensions.split('x').map(Number)
  return { length: length || 20, width: width || 8, height: height || 8 }
}

export default function ViewportCanvas({ container, items }: ViewportCanvasProps) {
  return (
    <div className="h-full w-full">
      <Canvas shadows>
        <PerspectiveCamera makeDefault position={[15, 10, 15]} fov={50} />
        <OrbitControls makeDefault enableDamping dampingFactor={0.05} />
        
        {/* Lighting */}
        <ambientLight intensity={0.5} />
        <directionalLight position={[10, 10, 5]} intensity={1} castShadow shadow-mapSize={[2048, 2048]} />
        <directionalLight position={[-10, 10, -5]} intensity={0.3} />
        
        {/* Grid Floor */}
        <Grid
          args={[50, 50]}
          cellSize={1}
          cellThickness={0.5}
          cellColor="#cccccc"
          sectionSize={5}
          sectionThickness={1}
          sectionColor="#999999"
          fadeDistance={50}
          fadeStrength={1}
          position={[0, 0, 0]}
        />
        
        {/* 3D Container */}
        <Suspense fallback={null}>
          {container && <Container3D container={container} items={items} />}
        </Suspense>
        
        {/* Measurement Axis */}
        <group position={[-8, 0, -8]}>
          {/* X Axis (Red) - Length */}
          <mesh position={[1.5, 0.05, 0]} rotation={[0, 0, Math.PI / 2]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>
          <mesh position={[3, 0.05, 0]} rotation={[0, 0, -Math.PI / 2]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#ff0000" />
          </mesh>
          
          {/* Y Axis (Green) - Height */}
          <mesh position={[0, 1.55, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>
          <mesh position={[0, 3.1, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#00ff00" />
          </mesh>
          
          {/* Z Axis (Blue) - Width */}
          <mesh position={[0, 0.05, 1.5]} rotation={[Math.PI / 2, 0, 0]}>
            <cylinderGeometry args={[0.02, 0.02, 3]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
          <mesh position={[0, 0.05, 3]} rotation={[Math.PI / 2, 0, 0]}>
            <coneGeometry args={[0.08, 0.2]} />
            <meshBasicMaterial color="#0000ff" />
          </mesh>
          
          {/* Axis Labels */}
          <Suspense fallback={null}>
            <Text position={[3.5, 0.05, 0]} fontSize={0.3} color="#ff0000" anchorX="left">
              X (Length)
            </Text>
            <Text position={[0, 3.5, 0]} fontSize={0.3} color="#00ff00" anchorY="bottom">
              Y (Height)
            </Text>
            <Text position={[0, 0.05, 3.5]} fontSize={0.3} color="#0000ff" anchorX="left">
              Z (Width)
            </Text>
          </Suspense>
        </group>
      </Canvas>
    </div>
  )
}

// Container 3D Component
function Container3D({ container, items }: { container: Container; items: any[] }) {
  const { length, width, height } = parseDimensions(container.dimensions)
  
  return (
    <group position={[0, height / 2, 0]}>
      {/* Container Wireframe Box */}
      <mesh>
        <boxGeometry args={[length, height, width]} />
        <meshStandardMaterial 
          color="#4a90e2" 
          wireframe={false}
          transparent 
          opacity={0.15}
        />
      </mesh>
      
      {/* Container Edges - stronger outline */}
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(length, height, width)]} />
        <lineBasicMaterial color="#2c5aa0" linewidth={2} />
      </lineSegments>
      
      {/* Floor of container */}
      <mesh position={[0, -height / 2 + 0.01, 0]} rotation={[-Math.PI / 2, 0, 0]} receiveShadow>
        <planeGeometry args={[length, width]} />
        <meshStandardMaterial color="#e8e8e8" side={2} />
      </mesh>
      
      {/* Container Info Label */}
      <Suspense fallback={null}>
        <Text
          position={[0, height / 2 + 0.5, 0]}
          fontSize={0.5}
          color="#2c5aa0"
          anchorY="bottom"
        >
          {container.name}
        </Text>
        <Text
          position={[0, height / 2 + 0.2, 0]}
          fontSize={0.3}
          color="#666666"
          anchorY="bottom"
        >
          {container.dimensions} ft • {container.weight}
        </Text>
      </Suspense>
      
      {/* Dimension Labels on Container Edges */}
      <Suspense fallback={null}>
        {/* Length label */}
        <Text
          position={[0, -height / 2 - 0.3, width / 2 + 0.3]}
          fontSize={0.4}
          color="#ff0000"
        >
          {length} ft
        </Text>
        {/* Width label */}
        <Text
          position={[length / 2 + 0.3, -height / 2 - 0.3, 0]}
          fontSize={0.4}
          color="#0000ff"
          rotation={[0, Math.PI / 2, 0]}
        >
          {width} ft
        </Text>
        {/* Height label */}
        <Text
          position={[length / 2 + 0.3, 0, width / 2 + 0.3]}
          fontSize={0.4}
          color="#00ff00"
        >
          {height} ft
        </Text>
      </Suspense>
      
      {/* Items */}
      {items.map((item, index) => (
        <Item3D key={item.id || index} item={item} containerHeight={height} />
      ))}
    </group>
  )
}

// Item 3D Component
function Item3D({ item, containerHeight }: { item: any; containerHeight: number }) {
  const colors = ['#ff6b6b', '#4ecdc4', '#45b7d1', '#f9ca24', '#6c5ce7', '#ff8c42', '#a8e6cf']
  const color = colors[Math.floor(Math.random() * colors.length)]
  
  // Parse item dimensions if they exist
  const itemLength = item.length || 1
  const itemWidth = item.width || 1
  const itemHeight = item.height || 1
  
  // Position items from bottom of container
  const x = item.x || 0
  const y = (item.y || itemHeight / 2) - containerHeight / 2
  const z = item.z || 0
  
  return (
    <mesh 
      position={[x, y, z]}
      castShadow
      receiveShadow
    >
      <boxGeometry args={[itemLength, itemHeight, itemWidth]} />
      <meshStandardMaterial color={color} />
      
      {/* Item edges for better visibility */}
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(itemLength, itemHeight, itemWidth)]} />
        <lineBasicMaterial color="#000000" linewidth={1} opacity={0.3} transparent />
      </lineSegments>
    </mesh>
  )
}